            </li>
          </ul>
          </div>
        </div>
      </div>
    </div>
  </template>

<script>
import { getcategoriesDate, getwrapData } from '@/api/home'

export default {
  name: 'homeIndex',
  data () {
    return {
      wrapList: [],
      categoriesList: []
    }
  },
  async created () {
    const res1 = await getwrapData()
    this.wrapList = res1

    const res2 = await getcategoriesDate()
    this.categoriesList = res2
  }
}
