<template>
    <div class="login">
      <div class="login-main">
        <div class="login-body">
          <!-- 登录页面左边图片 -->
          <div class="login-left">
            <div class="img-box">
              <img src="../../assets/images/login.jpg" alt="">
            </div>
          </div>
          <!-- 登录部分 -->
          <div class="login-right">
            <transition name="fade">
              <div v-show="isShow" :class="{'prompt': true, 'prompt-show-red': isRed, 'prompt-show-green': isGreen}">
                <i :class="{'iconfont': true, 'prompt-show-red': isRed, 'prompt-show-green': isGreen}">{{ promptIco }}</i>
                &nbsp;
                <span>{{ promptUser }}</span>
                <strong>{{ capUser }}</strong>
                <span>{{ promptMain }}</span>
              </div>
            </transition>
            <div class="form-box">
              <form action="">
                <div class="account">
                  <label for="acc">账号:</label>
                  <input type="text" id="icc" placeholder="用户名/邮箱" v-model="user.username">
                </div>
                <div class="kong10"></div>
                <div class="password">
                  <label for="pass">密码:</label>
                  <input type="password" id="pass" placeholder="密码" v-model="user.password">
                </div>
                <div class="captcha">
                  <label for="cap">验证码:</label>
                  <input type="text" id="cap" placeholder="右侧图形验证码中的数字" v-model="user.code">
                  <div class="cap-img">
                    <ValidCode @update:value="getCode"></ValidCode>
                  </div>
                </div>
                <div class="kong10"></div>
                <div style="height: auto; overflow: hidden; max-width: 400px;">
                  <div style="float: left;">
                    <input type="checkbox" v-model="isChecked">&nbsp;记住我
                    <span style="color: #bdbaba; padding-left: 10px; font-size: 12px;">公用电脑请勿勾选</span>
                  </div>
                  <div style="float: right;">
                    <a href="#" class="forgot-pass">忘记密码</a>
                  </div>
                </div>
                <div class="kong20"></div>
                <div class="form-submit">
                  <button type="submit" @click="login" >登录</button>
                  <a href="#">注册</a>
                </div>
              </form>
            </div>
            <div class="other-login">
              <div class="third">—— 使用第三方登录 ——</div>
              <div class="other-login-main">
                <a href="#" class="qq">
                  <i class="iconfont"></i>
                  &nbsp;
                  QQ登录
                </a>
                <a href="#" class="weibo">
                  <i class="iconfont"></i>
                  &nbsp;
                  微博登录
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 底部导航 -->
      <footer class="footer">
        <div class="footer-body">
          <div class="body-left">
            <h4>推荐栏目</h4>
            <div class="menua">
              <router-link :to="item.router" v-for="item in footerList" :key="item.id">
                {{ item.name }}
              </router-link>
            </div>
          </div>
          <div class="body-middle">
            <h4>联系方式</h4>
            <div class="menub">
              <p>新浪微博:&nbsp;&nbsp;<a href="#">喵次元</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p>
              <p>腾讯QQ:&nbsp;&nbsp;<a href="#">3203589083</a></p>
              <p>公众号:&nbsp;&nbsp;<a href="#">DIMTOWN</a></p>
            </div>
          </div>
          <div class="body-right">
            <h4>关于小镇</h4>
            <div class="menuc">
              <p>次元小镇是一个动漫分享平台,分享动漫资源、资讯、动漫美图壁纸、音乐和cosplay资源的小站，阿宅们快到碗里来ヽ(✿ﾟ▽ﾟ)ノ</p>
            </div>
          </div>
        </div>
      </footer>
      <div class="bottom">
        <div class="copy">
          <p>Copyright © 2014-2024  次元小镇 版权所有 </p>
        </div>
        <div class="bottom-logo">
          <router-link to="/home">
            <img src="../../assets/logo.png" alt="">
          </router-link>
        </div>
      </div>
    </div>
  </template>

<script>
import { getfooterData, getloginData } from '@/api/login'
import ValidCode from '@/components/ValidCode.vue'
import router from '@/router'
import Cookies from 'js-cookie'

export default {
  name: 'loginIndex',
  data () {
    return {
      code: '', // 验证码组件传递过来的code
      user: {
        code: '', // 表单里用户输入的code
        username: '',
        password: ''
      },
      loginList: [],
      footerList: [],
      isUsername: false,
      islogin: false,
      userId: '',
      isPassword: false,
      promptMain: '',
      promptUser: '',
      isRed: false,
      isGreen: false,
      isShow: false,
      promptIco: '',
      capUser: '',
      isChecked: false
    }
  },
  async created () {
    const res1 = await getfooterData()
    this.footerList = res1

    const res2 = await getloginData()
    this.loginList = res2

    this.getCookie()
  },
  components: {
    ValidCode
  },
  methods: {
    getCode (code) {
      this.code = code
    },
    getUser () {
      for (let i = 0; i < this.loginList.length; i++) {
        if (this.loginList[i].username === this.user.username) {
          this.isUsername = true
          this.userId = this.loginList[i].id
        }
      }
    },
    getPass () {
      this.getUser()
      if (this.isUsername) {
        if (this.loginList[this.userId].password === this.user.password) {
          this.isPassword = true
        }
      }
    },
    login () {
      this.getUser()
      this.getPass()
      this.capUser = this.user.username
      if (this.user.username === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptUser = ''
        this.capUser = ''
        this.promptMain = '请输入用户名或邮箱'
      } else if (this.user.password === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptUser = ''
        this.capUser = ''
        this.promptMain = '请输入密码'
      } else if (this.user.code === '') {
        this.isRed = true
        this.promptIco = ''
        this.promptUser = ''
        this.capUser = ''
        this.promptMain = '请输入验证码'
      } else {
        if (this.isUsername) {
          if (this.isPassword) {
            if (this.code.toLowerCase === this.user.code.toLowerCase) {
              this.isGreen = true
              this.promptIco = ''
              this.capUser = ''
              this.promptMain = '登录成功'
              if (this.isChecked) {
                Cookies.set('username', this.user.username, { expires: 30 })
                Cookies.set('password', this.user.password, { expires: 30 })
                Cookies.set('rememberMe', this.isChecked, { expires: 30 })
              } else {
                Cookies.remove('username')
                Cookies.remove('password')
                Cookies.remove('rememberMe')
              }
              router.push('/')
            } else {
              this.isRed = true
              this.promptIco = ''
              this.promptUser = ''
              this.capUser = ''
              this.promptMain = '验证码错误'
            }
          } else {
            this.isRed = true
            this.promptIco = ''
            this.promptUser = '为用户名'
            this.promptMain = '指定的密码不正确,请重新输入'
          }
        } else {
          this.isRed = true
          this.promptIco = ''
          this.promptUser = '用户名'
          this.promptMain = '未在本站点注册。请重新输入'
        }
      }
      this.user.code = ''
      this.isShow = true

      setTimeout(() => {
        this.isShow = false
      }, 2000)
    },
    getCookie () {
      const username = Cookies.get('username')
      const password = Cookies.get('password')
      const rememberMe = Cookies.get('rememberMe')

      this.user.username = username === undefined ? this.user.username : username
      this.user.password = password === undefined ? this.user.password : password
      this.isChecked = rememberMe === undefined ? false : Boolean(rememberMe)
    }
  }
}
</script>

<style lang="less" scoped>
  .login-main {
    width: 100%;
    max-width: 1180px;
    margin: 0 auto;
    padding-top: 20px;
    margin-top: 10px;
    // 左边图片部分
    .login-left {
      float: left;
