                vertical-align: bottom;
              }
            }
          }
        }
    }
  }
  .login-body::after {
    content: '';
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .8s;
  }
  .fade-enter, .fade-leave-to {
    opacity: 0;
  }
  // 底部导航
  .footer {
