<script>
export default {
  data () {
    return {
      isActive: false,
      isDisplay: true
    }
  },
  mounted: function () {
    window.addEventListener('scroll', this.handleScroll, true)
  },
  methods: {
    handleScroll () {
      const h = document.documentElement.scrollTop || document.body.scrollTop

      if (h > 100) {
        this.isActive = true
        this.isDisplay = false
      } else {
        this.isActive = false
        this.isDisplay = true
      }
    }
  }
}
