<!-- 轮播图组件 -->
<template>
    <div class="block">
        <el-carousel interval="2000" trigger="click" height="335px">
          <el-carousel-item v-for="item in images" :key="item.id">
            <a href="#"><img :src="item.idView" alt="" class="image"></a>
          </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
export default {
  data () {
    return {
      images: [
        { id: 0, idView: require('../assets/images/1.jpg') },
        { id: 1, idView: require('../assets/images/2.jpg') },
        { id: 2, idView: require('../assets/images/3.jpg') }
      ]
    }
  }
}
</script>

<style lang="less" scoped>
  .el-carousel__item img {
    width: 100%;
    height: auto;
    border-radius: 5px;
  }

</style>
